body{
  margin: 0;
  font-family: sans-serif;
  box-sizing: border-box;
  background-color: grey;
}

.card{
  display: flex;
  justify-content: center;
  align-items: center;
  font-size: 150px;
  background-color: black;
  color: white;
  padding: 0 20px;
  cursor: pointer;
  border-radius: 8px;
  min-width: 220px;
}

.card:hover{
  outline: 2px solid blue;
}

.cards{
  display: flex;
  gap: 20px;
  justify-content: center;
  flex-wrap: wrap;
}

.header{
  display: flex;
  justify-content: center;
  gap: 50px;
  margin: 10px 0;
}

.score{
  display: flex;
  gap: 50px;
}